
div {
  box-sizing: border-box;
}
.stating {
  margin-bottom: 45px;
  font-size: 12px;
  padding: 12px;
}
.statebox {
  width: 100%;
  height: auto;
  border-radius: 2px;
  padding: 6px;
  padding-top: 20px;
  border: 1px solid #ccc;
  margin-bottom: 20px;
}
.statebox h3 {
  font-size: 12px;
  height: 20px;
  line-height: 20px;
  margin-bottom: 12px;
}
.statebox h3 i {
  color: red;
  margin-left: 5px;
  margin-right: 5px;
}
.statebox h3 em {
  color: #2d4a58;
  margin-left: 5px;
  margin-right: 5px;
}
.statebox h3 span {
  font-size: 10px;
  float: right;
  margin-right: 12px;
}
.statebox .detailtext {
  height: auto;
  font-size: 13px;
  background: white;
  padding: 5px 0px 5px 0px;
  line-height: 18px;
  font-weight: bold;
  color: #34352c;
  font-family: "Arial", "Microsoft YaHei", "黑体", "宋体", sans-serif;
}
.statebox .imgs {
  width: 100%;
  height: 105px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 12px;
}
.statebox .imgs .mainimgs {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  width: 100px;
  height: 100px;
  margin-left: 5px;
  margin-right: 5px;
}
.statebox .imgs .mainimgs img {
  width: 100%;
  height: 100%;
}
.statebox .zan {
  font-size: 16px;
  color: #34352c;
}
.statebox .zan span {
  width: 20px;
  height: 20px;
  display: inline-block;
  background-image: url('../../../static/img/staticlove1.png');
  background-position: left top;
  background-size: 20px 20px;
  vertical-align: middle;
  margin-right: 3px;
}
.statebox .zan i {
  width: 20px;
  height: 20px;
  display: inline-block;
  background-image: url('../../../static/img/sharelove1.png');
  background-position: left top;
  background-size: 20px 20px;
  vertical-align: middle;
  margin-left: 20px;
  margin-right: 3px;
}
.statebox .zan em {
  width: 20px;
  height: 20px;
  display: inline-block;
  background-image: url('../../../static/img/cms.png');
  background-position: left top;
  background-size: 20px 20px;
  vertical-align: middle;
  margin-left: 20px;
  margin-right: 3px;
}
.statebox .comments {
  margin-top: 12px;
  height: auto;
  padding: 12px;
  padding-bottom: 6px;
}
.statebox .comments h2 {
  font-size: 16px;
  font-weight: bold;
}
.statebox .comments .commentsmsg {
  width: 100%;
  height: auto;
  margin-top: 12px;
  font-weight: bold;
  color: #2d4a58;
  line-height: 14px;
}
.statebox .comments .commentsmsg span {
  color: #a2b4ba;
  margin-left: 6px;
  font-weight: 100;
}



